import $ from 'jquery'

class Notes {
  constructor() {
    this.deleteBtn = document.querySelectorAll(".delete-note");
    // console.log(this.deleteBtn);
    this.events();
  }
  
  events() {
    
    // const deleteBtn =  Array.from(this.deleteNote);
    this.deleteBtn.forEach( btn => {
      btn.addEventListener("click", e => {
        console.log(this.deleteBtn);
        // e.preventDefault()
        this.deleteNote();
      })
    })
    // deleteBtn.map( btn => {
    //   btn.addEventListener("click", deleteNote);
    // })
  }

  // Methods
  deleteNote() {
      $.ajax({
        beforeSend: (xhr)  => {
          xhr.setRequestHeader('X-WP-Nonce', themeData.nonce);
        },
        url: themeData.root_url + 'wp-json/wp/v2/note/104',
        type: 'DELETE',
        success: (response) => {
          console.log('success');
          console.log(response);
        },
        error: (response) => {
          console.log('error');
          console.log(response);
        },
      })
  }
 }

 export default Notes;